@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body{
    background-color: #6a95f2;
}

.mainContainer{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.mainContainer .menu {
    margin-top: 1rem;
    border-radius: .5rem;
    background-color: #fff;
    box-shadow: 0 0 12px rgb(0 0 0 / 20%);
}

.mainContainer .menu ul {
    display: flex;
}

.mainContainer .menu li {
    cursor: pointer;
    list-style: none;
    padding: .5rem 1rem;
    transition: all .2s ease;
}

.mainContainer .menu li:first-child {
    border-radius: .5rem 0 0 .5rem;
}

.mainContainer .menu li:last-child {
    border-radius: 0 .5rem .5rem 0;
}

.mainContainer .menu li.active {
    color: #fff;
    background: rgb(106 149 242 / 80%);
}

.mainContainer .menu li:hover {
    color: #fff;
    background: rgb(106 149 242 / 80%);
}

.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.card{
    margin: 1rem;
    max-width: 20rem;
    min-width: 18rem;
    border-radius: .6rem;
    background-color: #fff;
}

.card .card-header {
    padding: .5rem 1rem;
    font-size: 1.6rem;
}

.card .card-header .sNo {
    display: grid;
    width: 1.6rem;
    height: 1.6rem;
    margin-top: .4rem;
    font-size: 1rem;
    border-radius: 5rem;
    place-items: center;
    color: rgb(0 0 0 / 60%);
    border: 2px solid rgb(0 0 0 / 60%);
}

.card .card-header .type {
    font-size: .9rem;
    margin-top: .4rem;
    border-radius: 5rem;
    text-transform: uppercase;
    color: rgb(0 0 0 / 60%);
}

.card .card-header h3 {
    text-align: left;
}

.card .card-body {
    display: grid;
    place-items: center;
}

.card .card-header .desc,
.card .card-header .line {
    font-size: .8rem;
}

.card .card-header .line {
    width: 100%;
    text-align: right;
    position: relative;
}

.card .card-header .line::before {
    content: '';
    top: 50%;
    left: 0;
    width: 72%;
    height: .01rem;
    position: absolute;
    background-color: rgb(0 0 0 / 20%);
}

.card .card-body .foodImage {
    max-width: 80%;
    max-height: 100%;
    overflow: hidden;
}

.card img {
    max-width: 100%;
}

.buyNow{
    width: 8rem;
    float: right;
    height: 2.5rem;
    cursor: pointer;
    font-size: .9rem;
    margin-bottom: 1rem;
    border-radius: .4rem;
    background-color: #fff;
    border: 2px solid #6a95f2;
    transition: all .2s ease-in-out;
}

.buyNow:hover {
    color: #fff;
    background-color: #6a95f2;
}